import axios from "axios";
import { Toast } from 'vant';
import router from '../router';

export function request(config){
    const instance = axios.create({
        baseURL: 'https://api.shop.eduwork.cn',  // 定义基础url
        timeout: 5000,  // 设置超时时间，请求5秒以上则取消请求
    })


    // 请求拦截
    instance.interceptors.request.use(config=>{
        // 如果有一些接口需要认证才可以访问，就在此处统一设置
        // 例如 token
        const token = window.localStorage.getItem('token');
        if(token){
            config.headers.Authorization = 'Bearer '+token;
        }

        return config;  // 放行
    }, err=>{  // 错误处理

    })


    // 响应拦截
    instance.interceptors.response.use(res=>{
        // 响应拦截时，返回数据成功通常直接放行
        // console.log(res);
        // return res;  // 放行
        return res.data ? res.data : res;  // 放行
    }, err=>{  // 错误处理
        // 如果有错误，这里会处理，通常根据错误的状态码进行书写，显示错误信息
        // 或者如果有需要授权才可访问的接口，统一跳转到login

        if(err.response.status == '401'){  // 未授权
            Toast.fail('请先登录');
            router.push({path: 'login'})
        }

        // console.log(err);
        // console.log(err.response.data);
        Toast.fail(err.response.data.errors[Object.keys(err.response.data.errors)[0]][0]);

    })

    return instance(config);

}