import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { Tag, Button, Image as VanImage, Card, Tab, Tabs, Swipe, SwipeItem, Lazyload, Badge, Sidebar, SidebarItem, Collapse, CollapseItem } from 'vant';
import {Form, Field, CellGroup} from 'vant'

createApp(App)
    .use(Swipe).use(SwipeItem).use(Lazyload, {
        loading: require('./assets/images/2.png')
    })
    .use(Badge).use(Sidebar).use(SidebarItem).use(Collapse).use(CollapseItem)
    .use(Tab).use(Tabs).use(Card).use(VanImage)
    .use(Tag).use(Button)
    .use(Form).use(Field).use(CellGroup)
    .use(store).use(router).mount('#app')
